 /* jshint esversion: 6 */
 import debug from '../package/debug/debug';
 import Swiper from 'swiper';
 const banner = (bannerSwiper) => {
   bannerSwiper = new Swiper('.banner .swiper-container', {
     nextButton: '.banner .swiper-button-next',
     prevButton: '.banner .swiper-button-prev',
     pagination: '.banner .swiper-pagination',
     paginationClickable: true,
     loop: true,
     speed: 2000,
     autoplay: 3000
   });
 };
 const about_pic = (aboutSwiper) => {
   aboutSwiper = new Swiper('.i-about .swiper-container', {
     pagination: '.i-about .swiper-pagination',
     paginationClickable: true,
     loop: true,
     speed: 2000,
     autoplay: 3000
   });
 };
 const news_tab = () => {
   $('body').off('click', '.index .i-news .tab label').on('click', '.index .i-news .tab label', function() {
     $('.index .i-news .tab label').removeClass('on');
     $(this).addClass('on');
   });
 };
 const project_swiper = (projectSwiper, previewSwiper) => {
   projectSwiper = new Swiper('.view', {
     speed: 3000,
     effect: 'fade',
     fade: {
       crossFade: false,
     },
     onSlideChangeStart: function() {
       updateNavPosition();
     }
   });
   previewSwiper = new Swiper('.preview', {
     //visibilityFullFit: true,
     watchSlidesProgress: true,
     watchSlidesVisibility: true,
     slidesPerView: '6',
     allowTouchMove: false,
     speed: 2000,
     onTap: function() {
       projectSwiper.slideTo(previewSwiper.clickedIndex);
     }
   });

   function updateNavPosition() {
     $('.preview .on').removeClass('on');
     var activeNav = $('.preview .swiper-slide').eq(projectSwiper.activeIndex);
     activeNav.addClass('on');
     // console.log($('.swiper-slide-visible.on').index());
      $('#line').removeClass().addClass('line' + activeNav.index()%5);
     if (!activeNav.next().hasClass('swiper-slide-visible')) {
       previewSwiper.slideTo(activeNav.index());
     } else if (!activeNav.prev().hasClass('swiper-slide-visible')) {
       // previewSwiper.slideTo(activeNav.index()-1);
       var thumbsPerNav = 5;
       previewSwiper.slideTo(activeNav.index() - thumbsPerNav);

     } else {

     }
   }
 };
 const init = (callback) => {
   callback(0);
   var bannerSwiper = null,
     aboutSwiper = null,
     projectSwiper = null,
     previewSwiper = null;
   debug('index is load');
   banner(bannerSwiper);
   about_pic(aboutSwiper);
   news_tab();
   project_swiper(projectSwiper, previewSwiper);
 };

 const index = {
   init: init
 };

 export default index;
