 /* jshint esversion: 6 */
 import debug from '../package/debug/debug';
 import {
   MP
 } from '../package/map/map';
 const init = (callback) => {
   callback(5);
   debug('contact is load');
   var _this = this;
   MP('bkI6awMBEolPtt9YqpAxDOYjsekCN717').then(BMap => {
     function initMap() {
       createMap(); //创建地图
       setMapEvent(); //设置地图事件
       addMapControl(); //向地图添加控件
       addMapOverlay(); //向地图添加覆盖物
     }

     function createMap() {
       map = new BMap.Map("map");
       map.centerAndZoom(new BMap.Point(126.712432, 45.603423), 18);
     }

     function setMapEvent() {
       map.enableKeyboard();
       map.enableDragging();
       map.enableDoubleClickZoom();
     }

     function addClickHandler(target, window) {
       target.addEventListener("click", function() {
         target.openInfoWindow(window);
       });
     }

     function addMapOverlay() {
       var markers = [{
         content: "",
         title: " 顺亿堂医药生物科技开发有限公司",
         imageOffset: {
           width: 0,
           height: 3
         },
         position: {
           lat: 45.603196,
           lng: 126.711848
         }
       }];
       for (var index = 0; index < markers.length; index++) {
         var point = new BMap.Point(markers[index].position.lng, markers[index].position.lat);
         var marker = new BMap.Marker(point, {
           icon: new BMap.Icon("http://api.map.baidu.com/lbsapi/creatmap/images/us_mk_icon.png", new BMap.Size(9, 25), {
             imageOffset: new BMap.Size(9, 25)
           })
         });
         var label = new BMap.Label(markers[index].title, {
           offset: new BMap.Size(25, 5)
         });
         var opts = {
           width: 200,
           title: markers[index].title,
           enableMessage: false
         };
         var infoWindow = new BMap.InfoWindow(markers[index].content, opts);
         marker.setLabel(label);
         addClickHandler(marker, infoWindow);
         map.addOverlay(marker);
       }
     }
     //向地图添加控件
     function addMapControl() {
       var scaleControl = new BMap.ScaleControl({
         anchor: BMAP_ANCHOR_BOTTOM_LEFT
       });
       scaleControl.setUnit(BMAP_UNIT_IMPERIAL);
       map.addControl(scaleControl);
       var navControl = new BMap.NavigationControl({
         anchor: BMAP_ANCHOR_TOP_LEFT,
         type: BMAP_NAVIGATION_CONTROL_LARGE
       });
       map.addControl(navControl);
       var overviewControl = new BMap.OverviewMapControl({
         anchor: BMAP_ANCHOR_BOTTOM_RIGHT,
         isOpen: true
       });
       map.addControl(overviewControl);
     }
     var map;
     initMap();
   });
 };

 const contact = {
   init: init
 };

 export default contact;
